import React from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
} from "@vkontakte/vkui";

const ExchangeSell = ({ go, id, durkaCoins, course }) => {
  return (
    <Div
      style={{
        margin: 10,
      }}
    >
      <Group>
        <Cell>
          <InfoRow header="Ваш баланс" className="balanceDCMiddle">
            <Div className="divFlexStart">
              <Div className="balanceDCMiddle">{durkaCoins}</Div>
              <Div className="iconDC" style={{ marginLeft: 4 }} />
            </Div>
          </InfoRow>

          <InfoRow className="balanceDCMiddle" header="Текущий курс">
            <Div className="divFlexStart">
              <Div className="iconDC" style={{ marginRight: 4 }} />
              <Div className="balanceDCMiddle"> = {course}/руб </Div>
            </Div>
          </InfoRow>

          <InfoRow header="Итоговая прибыль">
            <Div style={{ color: "green" }}>+25 руб </Div>
          </InfoRow>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 5,
            }}
          >
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
              Введите кол-во DurkaCoins для продажи
            </Div>
            <Input type="number"></Input>

            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
              Введите стоимость за 1 DurkaCoins в руб
            </Div>
            <Input type="number"></Input>
          </Div>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 10,
            }}
          >
            <Button mode="commerce" size="xl" stretched>
              Добавить предложение
            </Button>
          </Div>
        </Cell>
      </Group>
    </Div>
  );
};

export { ExchangeSell };
